/* General styles for the hero container */
.hero {
    position: relative;
    width: 100%;
    height: 500px;
    background-color: #004d99;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 50px 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    color: white;
}

/* Applying pixelated font style to all elements within .hero */
.hero * {
    font-family: 'Press Start 2P', cursive; /* Pixelated 'Press Start 2P' font */
    -webkit-filter: pixelate(5px); /* Chrome, Safari, Opera */
    filter: pixelate(5px); /* Standard syntax */
}

.progress-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 20px; /* Adjust if more space is needed */
    display: flex;
    align-items: center;
    z-index: 1000;
}

.correct-container {
    top: 0;
}

.incorrect-container {
    top: 20px; /* Adjust based on the height of the progress bars */
}

.correct-bar, .incorrect-bar {
    height: 20px;
    transition: width 0.4s ease;
}

.correct-bar {
    background-color: #39FF14; /* Neon green */
}

.incorrect-bar {
    background-color: red;
}

.progress-text {
    margin-left: 10px;
    color: white;
    font-size: 16px;
    white-space: nowrap;
}

/* Flexbox settings for the content area */
.hero-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 20px;
}

/* Styles for the start quiz button */
.grade-circle {
    padding: 10px 20px;
    margin: 10px;
    font-size: 1.5rem;
    cursor: pointer;
    background-color: red;
    color: white;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s, transform 0.3s;
}

.grade-circle:hover {
    background-color: #45a049;
    transform: scale(1.1);
}

/* Styles for the option buttons */
.option-button {
    display: inline-block;
    margin: 10px 20px;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    background-color: red;
    color: white;
    border: none;
    font-size: 0.9rem;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.3s;
}

.option-button:hover {
    background-color: #0056b3;
    transform: scale(1.1);
}

/* Responsive adjustments for smaller screens */
@media (max-width: 768px) {
    .hero {
        padding: 30px 15px;
    }

    .grade-circle {
        font-size: 1.2rem;
        padding: 8px 16px;
    }

    .option-button {
        font-size: 1rem;
        padding: 10px 15px;
    }
}
