/* Modal.css - Updated with Responsive Styles */

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1050;
}

.modal-content {
    background: white;
    width: 90%;
    max-width: 500px;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative;
    z-index: 1051;
}

.close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 24px;
    color: #333;
    cursor: pointer;
}

.form-label {
    display: block;
    margin-bottom: 10px;
    color: #333;
    font-size: 16px;
}

.form-input {
    width: 100%;
    padding: 8px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

.form-button {
    padding: 10px 20px;
    margin-top: 10px;
    background-color: #0056b3;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    transition: background-color 0.3s ease;
}

.form-button:hover {
    background-color: #004494;
}

.form-button.cancel {
    background-color: #777;
}

.form-button.cancel:hover {
    background-color: #555;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
    .modal-content {
        width: 95%;
        padding: 15px;
        max-width: 90%;  /* Allows the modal to take more space on smaller screens */
    }

    .form-label, .form-input, .form-button {
        font-size: 14px;  /* Slightly smaller font size for better fit on screens */
    }

    .form-input {
        padding: 6px;  /* Reduce padding for inputs to save space */
    }

    .form-button {
        padding: 8px 16px;  /* Smaller buttons on mobile */
    }

    .close-button {
        font-size: 20px;  /* Slightly smaller close button */
    }
}
